:root {
  --main-bg-color: #00acdc;
}

body {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
  display: block;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.logo_image {
  width: 40px;
  height: 40px;
  margin: 10px 0;
  display: block;
}

.link {
  cursor: pointer;
  text-decoration: underline;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_link:hover, .nav_link.is-active {
  color: var(--main-bg-color);
}

.nav_link.is-active:after {
  content: "";
  background-color: var(--main-bg-color);
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav_part {
  text-transform: uppercase;
  flex-direction: column;
  align-items: flex-start;
  font-size: 12px;
  text-decoration: none;
  display: flex;
}

.nav_part:not(:last-child) {
  margin-right: 20px;
}
